import '../styles/ActivityDetails.scss'
import { Fade } from 'react-reveal'

const ActivityDetails = () => {

    const nextActivities = 
                    [
                        {activity: 
                            {
                                location: 'Un endroit magique',
                                date: '5 avril 2022',
                                duration: '2 jours / 2 nuits',
                                price: '459 €',
                                maxPersons: '5 personnes'
                            }
                        }
                    ]

    const activityIcons = [{date: 'fa-solid fa-calendar-days'}, {location:'fa-solid fa-map-location-dot'}, {price: 'fa-solid fa-hand-holding-dollar'}, {maxPersons: 'fa-solid fa-users'}, {duration: 'fa-solid fa-heart'} ]

    const activitiesReactElements = nextActivities.map(activity => {

            
            // Turning the curent activity into an array.
            const activityDetails = Object.entries(activity.activity)

            //Iterating over the array of properties.
            const activityDetailsReactElements = activityDetails.map( (item) => {

                    //Getting proper Icon for current property.
                    const currentPropertyName = item[0];
                    const currentIcon =  activityIcons.filter(icon => icon.hasOwnProperty(currentPropertyName) === true)[0]

                    //Returning an icon and the associated label, inside a container. 
                    return (
                        <Fade bottom>
                        <div className = 'activity-details__container' >
                            <i  key = {currentPropertyName} className={currentIcon[currentPropertyName]}></i>
                            <div className = 'activity-details__label' key = {item[1]}>{item[1]}</div>
                        </div>
                        </Fade>
                    )
            })
            //Returning the current activity, ready to display.
            return activityDetailsReactElements
    })

    //For now we are returning only the fist activity, however later we'll probably display a selected activity in a list, unsing a useState hook to define the currently selected activity. 
    return (
        <div className = 'activity-details' >
                <div className = 'activity-details__title'>Notre prochain stage</div>
                {activitiesReactElements[0]}
              </div>
    )
}

export default ActivityDetails