import PageContent from "../../components/PageContent"
import { HOME } from "../constants/paths"
import homePageImage from '../../images/home.png'
import '../../styles/App.scss'
import SectionCard from "../../components/SectionCard"

import { ourProject } from "../../constants/constants"
import { ourRetreats } from "../../constants/constants"
import { melanie } from "../../constants/constants"
import { agathe } from "../../constants/constants"
import webee from '../../images/webee.png'
import ActivityDetails from "../../components/ActivityDetails"
import { Fade } from "react-reveal"


const Home = () => {

    return (
        <PageContent key={HOME}>
            <div className = 'home-content'>
              <img className = 'home__image' src = {homePageImage}/>
              <div className = 'home__title-container'>
              <Fade bottom><img className = 'home__webee' src = {webee} ></img></Fade>
                        <Fade>
                        <div className = 'home__title'>
                            <Fade top delay={400}><h1 className = 'home__title__element'>SEJOURS</h1></Fade>
                            <Fade bottom delay={600}><h1 className = 'home__title__element'>REGENERANTS</h1></Fade>
                        </div>
                        </Fade>
                    
              </div>
              
              
              <SectionCard id = 'project' fontAwesomeIcons={[{className: "fa-solid fa-leaf", color: "limegreen"}, {className: "fa-solid fa-heart-pulse", color: "orangered"}, {className: "fa-solid fa-signs-post", color: "#964B00"}]}  cardContent = {ourProject} style = {{'justify-content':'flex-start'}}/>
              <SectionCard id = 'activities' fontAwesomeIcons={[{className: "fa-solid fa-seedling", color: "lightgreen"}, {className: "fa-solid fa-person-hiking", color: "royalblue"}, {className: "fa-solid fa-tree", color: "green"}]} cardContent = {ourRetreats}/>
              <ActivityDetails/>
              <div></div>
              <SectionCard id = 'melanie' fontAwesomeIcons={[{className: "fa-solid fa-seedling", color: "limegreen"}, {className: "fa-solid fa-utensils", color: "lightblue"}, {className: "fa-solid fa-spa", color: "violet"}]} cardContent = {melanie} style = {{'justify-content':'flex-start'}}/>
              <SectionCard id = 'agathe' fontAwesomeIcons={[{className: "fa-solid fa-heart-pulse", color: "red"}, {className: "fa-solid fa-person-running", color: "lightblue"}, {className: "fa-solid fa-battery-full", color: "limegreen"}]} cardContent = {agathe}/>
            </div>

        </PageContent>
    )
}
export default Home